import { instance } from "./axiosConf";

export const packsAPI = {
    getPacks(payload: PacksRequestType) {
        // @ts-ignore
        return instance.get(`/cards/pack?pageCount=${payload.pageCount}&page=${payload.page}`, {
            params: {
                packName: payload.packName, //   
                min: payload.min, //   
                max: payload.max, //   
                sortPacks: payload.sortPacks,//   
                user_id: payload.user_id,
            }
        });
    },
    addPack(newPack: CardsPackType) {
        // @ts-ignore
        return instance.post('cards/pack', {cardsPack: {...newPack}})
    },
    deletePack(id: string) {
        return instance.delete(`cards/pack?id=${id}`)
    },
    updatePack(cardsPack: UpdatePackType) {
        return instance.put('cards/packs', cardsPack)
    }
}

type CardsPackType = {
    name?: string
    deckCover?: string 
    private: boolean
}

type UpdatePackType = {
    _id: string
    name: string
}


type PacksRequestType = {
    packName?: string //   
    min?: number, //   
    max?: number, //   
    sortPacks?: string,//   
    page?: number, //   
    pageCount?: number, //   
    user_id?: string,
}

type PacksResponseType = {
    cardPacks: [
        {
            _id: string
            user_id: string
            name: string
            cardsCount: number
            created: string
            updated: string
        },
    ]
    cardPacksTotalCount: number
    //   
    maxCardsCount: number
    minCardsCount: number
    page: number //   
    pageCount: number
    //    

}